import React, { useState } from "react";
import { auth, db } from "../firebase";
import "./SellerReg.css";
import { Redirect, Route, useHistory } from "react-router-dom";
import { Link } from 'react-router-dom';

function Sellerreg() {
    const history = useHistory("");
    const [sellerName, setSellerName] = useState("");
    const [mobileNo, setMobileNo] = useState("");
    const [shopName, setShopName] = useState("");
    const [shopAddress, setshopAddress] = useState("");
    const [city, setCity] = useState("");
    const [password, setPassword] = useState("");

    const register = (event) => {
        event.preventDefault();
        const email = mobileNo+"@abc.cdf"

        auth
            .createUserWithEmailAndPassword(email, password)
            .then((auth) => {
                if (auth.user) {
                    auth.user.updateProfile({
                    }).then((e) => {
                        db.collection('user').doc(auth.user.uid).set({
                            uid: auth.user.uid,
                            sellerName: sellerName,
                            shopName: shopName,
                            shopAddress: shopAddress,
                            mobileNumber: mobileNo,
                            city: city,
                            type: "seller"
                        })
                            .then((event) => {
                                history.push("/sellerstock")
                                
                            })
                    })
                }
            })
            .catch((e) => {
                if (
                    e.message ===
                    "The password is invalid or the user does not have a password."
                ) {
                    alert("Please check your credentials again");
                } else if (
                    e.message ===
                    "A network error (such as timeout, interrupted connection or unreachable host) has occurred."
                ) {
                    alert("Unable to connect. Check your Network and try again");
                } else if (
                    e.message === 
                    "A network error (such as timeout, interrupted connection or unreachable host) has occurred.")
                    {
                        alert("A network problem has occured")
                    } else if (
                    e.message ===
                    "There is no user record corresponding to this identifier. The user may have been deleted."
                ) {
                    history.push("/register");
                    window.scrollTo({
                        top: document.body.scrollHeight,
                        left: 0,
                        behavior: "smooth",
                    }
                );
                }
            });
    };

    return (
        <div className="register">
      <img src="https://spandanmanna.github.io/FreshVeg2Home/Logo3.jpg" class="register__logo" />
                <div className="register__container">
                <h1>Create Account as Seller</h1>
                <div className="hr3" />
                <form>
                    <div className="row">
                        <input
                            onChange={(e) => {
                                setSellerName(e.target.value);
                            }}
                            className="register__name"
                            type="text"
                            placeholder="Full Name"
                        />
                                                <input
                            onChange={(e) => {
                                setMobileNo(e.target.value);
                            }}
                            type="number"
                            placeholder="Mobile Number"
                        />

                    </div>
                    <center>
                    <input
                            onChange={(e) => {
                                setShopName(e.target.value)
                            }}
                            className="register__name"
                            type="text"
                            placeholder="Shop Name"
                        />
                        <input
                            onChange={(e) => {
                                setShopName(e.target.value)
                            }}
                            className="register__name"
                            type="text"
                            placeholder="Shop Address"
                        />
                    </center>
                    <center>
                     
                        <input
                            onChange={(e) => {
                                setCity(e.target.value)
                            }}
                            className="register__name"
                            type="text"
                            placeholder="City"
                        />

                    </center>
                    <center>
                        <input
                            onChange={(event) => setPassword(event.target.value)}
                            type="text"
                            placeholder="Set Password"
                        />
                    </center>

                    <p className="register__policy">
                        By clicking Sign Up, you agree to {" "}
                        <span>Take Order</span> and <span>Home Delivery</span> to your valued customer
          </p>

                    <center>
                        <button
                            onClick={register}
                            type="submit"
                            className="register__register"
                        >
                            Sign Up

            </button>
                    </center>
                    <Link to="/login">
                        <p className="register__login">Already have an account?</p>
                    </Link>
                </form>
            </div>
        </div >
    );
}

export default Sellerreg;