import React, { useEffect, useState } from "react";
import "./App.css";
import Login from "./Components/Login";
import Buyerreg from "./Components/Buyerreg";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import { auth } from "./firebase";
import PhoneLogin from "./Components/Otp";
import Sellerreg from './Components/Sellerreg'
import SellerStock from "./Components/SellerStock";
import Cart from "./Components/Cart";
import Door from "./Components/Door";
import Extra from "./Components/extra";


function App() {

  const [user, setUser] = useState([]);

  useEffect(() => {
    auth.onAuthStateChanged((authUser) => {

      if (authUser) {
        setUser(authUser);
      } else {
        setUser(false);
      }
    })
  }, [])


  return (
    < div >
      < div className="app" >
        <Router>
          <Switch>
            <Route path="/login">
              <Login user={user}/>
            </Route>
            <Route path="/otp">
              <PhoneLogin />
            </Route>

            <Route path="/register">
              <Buyerreg />
            </Route>

            <Route path="/sellerreg">
              <Sellerreg />
            </Route>

            <Route path="/sellerstock">
              <SellerStock />
            </Route>

            <Route path="/cart">
              <Cart />
            </Route>

            <Route path="/gateway">
              <Door />
            </Route>

            <Route path="/extra">
              <Extra />
            </Route>

            <Route path="/">
            <Login user={user} selected/>
            </Route>
 
          </Switch>
        </Router>
      </div >
    </div >
  );
}

export default App